@import url(https://fonts.googleapis.com/css?family=IBM+Plex+Mono:100,200,300,400,500,600,700,800);
@import url(https://fonts.googleapis.com/css?family=Azeret+Mono::100,200,300,400,500,600,700,800);
@import url(https://fonts.googleapis.com/css?family=Space+Mono::100,200,300,400,500,600,700,800);
@import url(https://fonts.googleapis.com/css?family=Roboto+Mono::100,200,300,400,500,600,700,800);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
        'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    background-color: rgb(255, 255, 255);
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.flex-container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
}
.ascii {
    font-family: 'monospace';
    color: black;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 60%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    min-height: 400px;
    min-width: 300px;

    padding: 40px;
    z-index: -1;
}

/* large background text */
/* fixed postion, doesn't affect other elemnts of the DOM*/

.background-text {
    font-family: 'Helvetica';
    font-size: 100px;
    width: 800px;

    position: fixed;
    top: 10%;
    left: 85%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color: white;
    opacity: 0.7;
}

.flex-row {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    font-size: 12px;
    color: grey;
    margin-top: 20px;
    padding-left: 20px;
}

.menu {
    width: 150px;
    text-align: left;
    z-index: 100;
    background-color: rgb(255, 255, 255, 0.7);
}

.info-box {
    border: 1px solid grey;
    max-width: 200px;
    height: 100px;
    padding: 5px;
}

.menu-entry {
    padding-bottom: 20px;
}

.menu-entry-label {
    padding-top: 10px;
    padding-bottom: 10px;
}

/* i'm not sure what this is for but i think it matters */
input[type='file'] {
    position: absolute;
    left: -99999rem;
}

input[type='button'] {
    position: absolute;
    left: -99999rem;
}

.slider- terix {
    -webkit-user-select: none;
            user-select: none;
    height: 10px;
    width: 10px;
}
.slider-asterix:hover {
    color: cornflowerblue;
}

.slider-track {
    font-size: 15px;
    color: blue;
    -webkit-user-select: none;
            user-select: none;
}

.slider-input {
    -webkit-appearance: none;
    width: 80px;
    background: transparent;
    outline: none;
    opacity: 0;
    transition: opacity 0.2s;

    position: absolute;
    left: 25px;

    margin-top: -2px;
}

.slider-input:hover {
    /* opacity: 1; */
}

.slider-input::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;

    width: 20px;
    height: 20px;
    border-radius: 100%;
    background-color: blue;
    cursor: grab;

    margin-bottom: 10px;
}

/* buttons, like 'save to clipboard', and 'upload file' */
.clickable-button {
    border: 1px solid rgb(200, 200, 200, 1);
    padding: 2px;
    padding-left: 10px;
    padding-right: 10px;
    cursor: pointer;
    border-radius: 2px;
}

.clickable-button:hover {
    background-color: lightgrey;
}

.highlighted-element {
    /* display a red exclamtion mark */
    color: blue;
    font-size: 30px;
    position: relative;
    top: -2px;
    left: 5px;
}

.hidden-button {
    position: absolute;
    left: -99999rem;
}

/* check boxes */
.checkboxes label {
    display: block;
}

.checkboxes input {
    width: 25px;
    height: 15px;
    padding: 0;
    margin: 0;
    vertical-align: bottom;
    position: relative;
    top: -1px;
}

